<template>
    <div>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    fixed
                    prop="id"
                    label="ID"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="标题"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="author"
                    label="作者"
                    width="120">
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="100">
                <template slot-scope="scope">
                    <el-button @click="showEditBook(scope.row)" type="text" size="small">修改</el-button>
                    <el-button @click="delBook(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                :page-size=pageSize
                :pager-count=totalPages
                layout="prev, pager, next"
                :total=total
                @current-change="page">
        </el-pagination>

        <el-dialog title="图书信息" :visible.sync="dialogFormVisible">
            <el-form :model="form" :rules="rules" ref="ruleForm" >
                <el-form-item label="图书名称" :label-width="formLabelWidth" prop="name">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="作者" :label-width="formLabelWidth" prop="author">
                    <el-input v-model="form.author" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="editBook('ruleForm')">修 改</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        methods: {
            Msg(msg,type) {
                this.$message({
                    message: msg,
                    type: type
                });
            },
            showEditBook(row) {
                this.dialogFormVisible = true;
                this.form.name = row.name;
                this.form.author = row.author;
                this.form.id=row.id;

            }, editBook(form) {
                this.$refs[form].validate((valid) => {
                    const _this = this;
                    if (valid) {
                        axios.put('http://localhost:8181/book/editBook',this.form).then(function(resp){
                            if(resp.data=="success"){
                                _this.Msg('修改图书成功','success');
                                _this.dialogFormVisible = false;
                                window.location.reload();
                            }else{
                                _this.Msg('修改图书失败','error');
                            }
                        })
                    }
                });
            },
            page(currentPage) {
                const _this = this;
                axios.get('http://localhost:8181/book/findAll/' + currentPage).then(function (resp) {
                    _this.tableData = resp.data.content;
                })
            },
            delBook(row) {
                const _this = this
                this.$confirm('此操作将永久删除该信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    axios.post('http://localhost:8181/book/delBook', row).then(function (resp) {
                        window.location.reload();
                    });
                    this.$message({
                        type: 'success',
                        message: '删除成功!',
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        },
        data() {
            return {
                total: null,
                tableData: null,
                pageSize: null,
                totalPages: null,
                dialogFormVisible: false,
                form: {
                    id:null,
                    name:null,
                    author:null
                },
                formLabelWidth: '120px',
                rules: {
                    name: [
                        {required: true, message: '请输入图书名称', trigger: 'blur'},
                        {min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur'}
                    ],
                    author: [
                        {required: true, message: '请输入图书作者', trigger: 'blur'},
                        {min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur'}
                    ]
                }
            };
        }
        ,
        created() {
            const _this = this;
            axios.get('http://localhost:8181/book/findAll/1').then(function (resp) {
                _this.tableData = resp.data.content;
                _this.total = resp.data.totalElements;
                _this.pageSize = resp.data.size;
                _this.totalPages = resp.data.totalPages;
            })
        }
    }
</script>